body{
    font-family: 'Open Sans', sans-serif;
}

img{
    max-width: 100%;
    max-height: 100%;
}
/* Header css and navigation css*/

.navbar-expand-md .navbar-toggler {
    display: block;
    margin-left: auto;
}

.header-section {
    position: fixed;
    z-index: 999;
    right: 0;
    left: 0;
    padding-left: 130px;
}

button.navbar-toggler span {
    width: 39px;
    height: 3px;
    background-color: #ffffff;
    display: block;
    margin: 8px 0;
}


button.navbar-toggler:focus {
    outline: 0;
    border: 0;
}

.header-navigation ul li {
    font-size: 38px;
    padding-bottom: 30px;
    text-align: center;
}

.header-navigation ul {
    list-style-type: none;
    padding: 0;
}

.header-navigation {
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    z-index: 99;
    bottom: 0;
    background: #3C3B3F;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #605C3C, #3C3B3F);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(right, #605C3C, #3C3B3F);
    background: linear-gradient(to left, #605C3C, #3C3B3F); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.header-navigation ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: fixed;
    right: 0;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.hidenav.header-navigation {
    -webkit-transform: translate(100%,0);
            transform: translate(100%,0);
    opacity: 0;
    -webkit-transition: 0.9s cubic-bezier(0.05, 1.25, 0.35, 1.34);
    transition: 0.9s cubic-bezier(0.05, 1.25, 0.35, 1.34);
}

.shownav.header-navigation{
    -webkit-transform: translate(0,0);
            transform: translate(0,0);
    opacity: 1;
    -webkit-transition: 0.9s cubic-bezier(0.05, 1.25, 0.35, 1.34);
    transition: 0.9s cubic-bezier(0.05, 1.25, 0.35, 1.34);
}


/* Banner css */

section.banner-section {
    height: 100vh;
    background-color: #000000;
    padding-left: 130px;
    overflow-x: hidden;
}

section.banner-section.clearfix .row {
    height: 100vh;
}

.profle-image img {
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}

.social-side-bar {
    width: 100px;
    position: fixed;
    top: 0;
    bottom: 0;
    background-color: #292929;
}

nav.navbar.navbar-expand-md.navbar-dark > a {
    font-size: 23px;
    font-weight: 600;
}

.header-navigation ul li > button {
    background-color: transparent;
    border: 0;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 0.10em;
}

.heading-block h1 > span {
    display: block;
}

.heading-block h1 {
    color: #ffffff;
    font-size: 90px;
    text-shadow: 2px 2px #795548;
}

.heading-block {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    max-width: 500px;
    margin: 0 auto;
    z-index: 9;
    position: absolute;
    top: 50%;
}

.heading-block h1 > span {
    color: #ffffff;
    font-weight: 600;
    text-align: center;
    font-size: 48px;
    margin: 7px 0;
    background-color: #352424;
    padding: 10px 0;
    font-style: italic;
}

a.section-down-arrow {
    width: 60px;
    height: 60px;
    border: 2px solid #ffffff;
    display: block;
    border-radius: 50%;
    position: absolute;
    right: 0;
    left: 48%;
    bottom: 56px;
}

a.section-down-arrow:after {content: '';position: absolute;border: 5px solid #ffffff;text-align: right;right: 0;left: 34%;top: 32%;width: 17px;height: 17px;border-top: 0;border-left: 0;-webkit-transform: rotate(45deg);transform: rotate(45deg);font-weight: 600;}

/* Style the video: 100% width and height to cover the entire window */
#myVideo {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
  }

